import Typography from "@mui/material/Typography";
import Timeline from "@mui/lab/Timeline";
import TimelineItem from "@mui/lab/TimelineItem";
import TimelineSeparator from "@mui/lab/TimelineSeparator";
import TimelineConnector from "@mui/lab/TimelineConnector";
import TimelineContent from "@mui/lab/TimelineContent";
import TimelineDot from "@mui/lab/TimelineDot";
import Paper from "@mui/material/Paper";
import TimelineOppositeContent from "@mui/lab/TimelineOppositeContent";
import Container from "@mui/material/Container";
import { useTheme } from "@mui/material/styles";
import Link from "@mui/material/Link";
import GroupsIcon from "@mui/icons-material/Groups";
import PersonIcon from "@mui/icons-material/Person";
import SectionTitle from "./components/SectionTitle";

interface Work {
  title: string;
  description: string;
  bulletPoints: string[];
  persional: boolean;
  start: Date;
  end?: Date;
  links?: string[];
}

const WORKS: Work[] = [
  {
    title:
      "",
    description: "",
    bulletPoints: [
      "",
      ".NET CorePostgreSQL",
      "(DDD)",
      "RESTful API",
      "Redis",
      "React",
      "(Windows)",
    ],
    persional: false,
    start: new Date(2021, 4, 0),
  },
  {
    title: "",
    description: "",
    bulletPoints: [
      "",
      "Spring Boot",
      "Spring Data JPA",
      "React Native",
      "React, React Redux",
      "UI/UX",
    ],
    persional: true,
    start: new Date(2021, 3, 0),
    links: [
      "https://github.com/bin-jump/petfabula_backend",
      "https://github.com/bin-jump/petfabula_client",
      "https://github.com/bin-jump/petfabula_admin",
    ],
  },

  {
    title: "",
    description: "",
    bulletPoints: [
      "",
      ".NET CorePostgreSQL",
      "React",
      "",
    ],
    persional: false,
    start: new Date(2019, 4, 0),
    end: new Date(2021, 3, 0),
  },

  {
    title: "",
    description: "",
    bulletPoints: [
      "",
      "GC",
      "",
      "Windows",
    ],
    persional: false,
    start: new Date(2017, 8, 0),
    end: new Date(2019, 3, 0),
  },
];

const WorkItem = ({ work }: { work: Work }) => {
  const { title, description, bulletPoints, persional, links } = { ...work };
  const theme = useTheme();

  return (
    <Paper elevation={0} style={{ textAlign: "left", marginBottom: 40 }}>
      <Typography
        variant="subtitle2"
        component="span"
        style={{ fontWeight: "bold" }}
      >
        {persional ? `${title}` : title}
      </Typography>

      <Typography>{description}</Typography>

      <ul style={{ listStyleType: "disc" }}>
        {bulletPoints.map((item) => (
          <li key={item}>
            <Typography variant="body2">{item}</Typography>
          </li>
        ))}
      </ul>

      {links && (
        <div style={{ marginTop: 12, marginLeft: 6 }}>
          <Typography variant="caption">Links:</Typography>
          {links.map((item) => (
            <Link
              key={item}
              underline="always"
              style={{
                display: "block",
                fontSize: 12,
                color: theme.palette.secondary.main,
              }}
              href={item}
              target="_blank"
              rel="noopener"
            >
              {item}
            </Link>
          ))}
        </div>
      )}
    </Paper>
  );
};

const dateStr = (date?: Date) => {
  return date ? `${date.getFullYear()}${date.getMonth() + 1}` : "";
};

export default function Works() {
  const theme = useTheme();

  return (
    <div
      id="works"
      style={{ padding: 80, backgroundColor: theme.palette.background.paper }}
    >
      <Container>
        <SectionTitle title={"Works"} />
      </Container>

      <div>
        <Timeline position="alternate">
          {WORKS.map((item) => (
            <TimelineItem key={item.title}>
              <TimelineOppositeContent
                sx={{ m: "auto 0" }}
                align="right"
                variant="body2"
                color="text.secondary"
              >
                <Typography
                  sx={{ color: theme.palette.grey[500] }}
                  variant="caption"
                >
                  {`${dateStr(item.start)}  ${dateStr(item.end)}`}
                </Typography>
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot variant="outlined">
                  {item.persional ? (
                    <PersonIcon
                      style={{ color: theme.palette.text.secondary }}
                    />
                  ) : (
                    <GroupsIcon
                      style={{ color: theme.palette.text.secondary }}
                    />
                  )}
                </TimelineDot>
                <TimelineConnector />
              </TimelineSeparator>
              <TimelineContent>
                <WorkItem work={item} />
              </TimelineContent>
            </TimelineItem>
          ))}
        </Timeline>
      </div>
    </div>
  );
}
